.page {
    color: #000;
    
    &__wrapper {
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
        padding-left: 20px;
        padding-right: 20px;
    }

    &__header {
        margin: 0 auto 30px;
        padding: 30px 0 10px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    &__logo {
        width: 40px;
        height: 40px;
    }

    &__nav {
        margin-left: 100px;

        @media @mobile {
            margin-top: 10px;
            margin-left: 20px;
        }
    }

    &__intro {
        margin-bottom: 100px;

        @media @mobile {
            margin-bottom: 50px;
        }
    }

    &__main {}

}