* {
  box-sizing: border-box;
  font-family: 'Jost', sans-serif;
}
.page {
  color: #000;
}
.page__wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
.page__header {
  margin: 0 auto 30px;
  padding: 30px 0 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.page__logo {
  width: 40px;
  height: 40px;
}
.page__nav {
  margin-left: 100px;
}
@media (max-width: 601px) {
  .page__nav {
    margin-top: 10px;
    margin-left: 20px;
  }
}
.page__intro {
  margin-bottom: 100px;
}
@media (max-width: 601px) {
  .page__intro {
    margin-bottom: 50px;
  }
}
.logo {
  position: relative;
  border-radius: 50% 0 50% 50%;
  background-color: #8643DC;
}
.logo__logo-text {
  position: relative;
  z-index: 100;
  padding-top: 5px;
  color: #fff;
  font-size: 26px;
  font-weight: bold;
  text-align: center;
}
.logo__inner-logo {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 50;
  width: 60%;
  height: 60%;
  border-radius: 50% 0 50% 0%;
  background-color: #FF59C1;
}
.logo__link {
  display: inline-block;
  padding: 9px 49px;
  text-decoration: none;
  color: #FF59C1;
  font-size: 1.3rem;
}
.logo__link:hover {
  text-decoration: underline;
  color: #8643DC;
}
.logo__link:focus {
  text-decoration: underline;
  color: #8643DC;
}
.button {
  color: #fff;
  background: #8643DC;
  border-radius: 100px;
  padding: 18px 50px;
  font-weight: 500;
  font-size: 18px;
  border: none;
  cursor: pointer;
}
.button:hover {
  background: #FF59C1;
}
.button:focus {
  background: #FF59C1;
}
.button:active {
  box-shadow: inset 0px 0px 5px 1px #FF59C1;
  background: #ff8cd4;
}
.button--hidden {
  display: none;
}
.nav {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 15px;
}
@media (max-width: 601px) {
  .nav {
    display: block;
  }
}
@media (max-width: 601px) {
  .nav__item {
    margin-bottom: 15px;
  }
}
.nav__link {
  font-size: 1.1rem;
  letter-spacing: 0.2px;
  font-weight: 500;
  color: #7E7F86;
  text-decoration: none;
}
.nav__link:hover {
  color: #47444E;
}
.nav__link:focus {
  color: #47444E;
}
.intro {
  padding: 50px 0 0;
  display: flex;
  gap: 50px;
}
@media (max-width: 601px) {
  .intro {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    display: block;
  }
}
.intro__text-box {
  max-width: 40%;
}
@media (max-width: 601px) {
  .intro__text-box {
    max-width: 100%;
  }
}
.intro__title {
  margin-top: 0;
  margin-bottom: 33px;
  color: #1B1C23;
  font-size: 2.25rem;
  letter-spacing: 1px;
}
.intro__text {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.25rem;
  line-height: 170%;
}
.intro__image-box {
  max-width: 50%;
  text-align: center;
}
@media (max-width: 601px) {
  .intro__image-box {
    max-width: 100%;
    margin: 0 auto;
  }
}
.intro__image {
  max-width: 100%;
}
.content__title {
  font-weight: normal;
  font-size: 1rem;
  color: #8643DC;
  margin-top: 0;
  margin-bottom: 40px;
}
@media (max-width: 601px) {
  .content__title {
    text-align: center;
    border-bottom: 2px solid #8643DC;
    padding-bottom: 2px;
  }
}
.content__title::before {
  content: "____";
  position: absolute;
  font-weight: normal;
  color: #8643DC;
  font-size: 1.6rem;
}
@media (max-width: 601px) {
  .content__title::before {
    content: '';
  }
}
.content__subtitle {
  margin-bottom: 0;
  color: #1B1C23;
  font-size: 2rem;
  letter-spacing: 1px;
}
.content__subtitle--mb33 {
  margin-top: 0;
  margin-bottom: 33px;
}
.content__subtitle--mb23 {
  margin-top: 0;
  margin-bottom: 23px;
}
.content__grid {
  display: flex;
  gap: 30px;
  margin-bottom: 100px;
}
@media (max-width: 601px) {
  .content__grid {
    flex-wrap: wrap;
    margin-bottom: 50px;
  }
}
.content__gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 100px;
}
@media (max-width: 601px) {
  .content__gallery {
    margin-bottom: 50px;
  }
}
.content__technologies {
  margin-bottom: 100px;
}
@media (max-width: 601px) {
  .content__technologies {
    margin-bottom: 50px;
  }
}
.content__about {
  margin: 30px 0 100px;
}
@media (max-width: 601px) {
  .content__about {
    margin-bottom: 50px;
  }
}
.tech__title {
  margin-top: 0;
  margin-bottom: 20px;
  padding-left: 60px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: 500;
  color: #1B1C23;
  font-size: 24px;
  letter-spacing: 0.4px;
  background-size: contain;
  background-repeat: no-repeat;
}
.tech__title--html {
  background-image: url(../images/html.svg);
}
.tech__title--css {
  background-image: url(../images/css.svg);
}
.tech__title--js {
  background-image: url(../images/js.svg);
}
.tech__description {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 200%;
  color: #47444E;
}
@media (max-width: 601px) {
  .gallery {
    display: block;
  }
}
.gallery__moreButton {
  margin: 30px auto;
}
@media (max-width: 601px) {
  .gallery__moreButton {
    display: block;
  }
}
.example {
  flex: 1 1 calc((100% - 20px) / 2);
  padding: 30px 45px 0;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
@media (max-width: 601px) {
  .example {
    border-radius: 0;
  }
}
.example--yellow {
  background: #fff2d9;
}
.example--blue {
  background: #F3F1FA;
}
.example--blue2 {
  background: rgba(51, 103, 153, 0.2);
}
.example--cerin {
  background: rgba(187, 133, 255, 0.2);
}
.example--pink {
  background: #fff4fb;
}
.example--gaiszils {
  background: #d8ecff;
}
.example--hidden {
  display: none;
}
.example__title {
  padding-left: 0;
  padding-top: 0;
  padding-bottom: 5px;
  margin-top: 0;
  margin-bottom: 5px;
}
.example__header {
  display: flex;
}
.example__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-top: 0;
  margin-bottom: 20px;
  width: 60%;
  font-size: 1rem;
}
.example__list-item {
  margin-bottom: 1px;
}
.example__list-item:first-child {
  color: #8643DC;
  font-weight: 500;
  margin-bottom: 3px;
}
.example__logo {
  width: 18px;
  height: 18px;
  align-self: flex-end;
  margin-bottom: 15px;
  margin-left: 12%;
}
.example__link {
  text-decoration: none;
  color: #FF59C1;
  font-size: 16px;
  padding: 0 0 0 22px;
}
.example__link:hover {
  text-decoration: underline;
  color: #8643DC;
}
.example__link:focus {
  text-decoration: underline;
  color: #8643DC;
}
.example__img {
  max-width: 100%;
}
.lessons {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 601px) {
  .lessons {
    display: block;
  }
}
.lessons__image-box {
  max-width: 50%;
  text-align: center;
  padding-top: 50px;
}
@media (max-width: 601px) {
  .lessons__image-box {
    margin: 0 auto;
    padding-top: 20px;
  }
}
.lessons__image {
  max-width: 100%;
}
.lessons__schema {
  width: 50%;
}
@media (max-width: 601px) {
  .lessons__schema {
    width: 100%;
  }
}
.lessons__mes-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin: 40px 0 0 0;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
}
.lessons__mes-item {
  display: inline-block;
}
.lessons__mes-item--es {
  width: 40%;
}
.lessons__mes-item--jus {
  width: 50%;
}
.lessons__order-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin: 20px 0 40px;
  padding: 0 0 50px;
  background-image: url(../images/schema.png);
  background-image: url(../images/schema.webp);
  background-repeat: no-repeat;
  background-position: 45% 0px;
  background-size: contain;
}
@media (min-resolution: 144dpi) {
  .lessons__order-list {
    background-image: url(../images/schema@2x.png);
    background-image: url(../images/schema@2x.webp);
  }
}
.lessons__order-item--left {
  padding-left: 10px;
  width: 40%;
}
.lessons__order-item--right {
  width: 40%;
  margin-left: 55%;
}
.lessons__order-item--m20 {
  margin-top: 20px;
}
.lessons__order-item--m10 {
  margin-top: 10px;
}
.technologies {
  display: flex;
  justify-content: center;
  gap: 35px;
  background: #fff4fb;
  background: linear-gradient(to right, rgba(187, 133, 255, 0.53) 0%, #fff4fb 100%);
  padding: 5px 10px;
}
.technologies__item {
  width: 50px;
}
.about {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 10px;
}
@media (max-width: 601px) {
  .about {
    flex-wrap: wrap;
  }
}
.about__card {
  flex-basis: 300px;
  flex-grow: 1;
  padding: 7px;
  background: linear-gradient(to bottom, #fff4fb 0%, rgba(255, 244, 251, 0.9) 38%, rgba(187, 133, 255, 0.83) 100%);
}
.about__title {
  margin-top: 0;
  margin-bottom: 20px;
  padding-top: 10px;
  font-weight: 500;
  color: #1B1C23;
  font-size: 1rem;
  letter-spacing: 0.4px;
}
.about__details {
  line-height: 1.5;
  margin-top: 0;
  margin-bottom: 16px;
}
.serts {
  display: flex;
  gap: 10px;
  padding-bottom: 2px;
  margin-bottom: 0;
}
.serts__link {
  text-decoration: none;
  color: #8643DC;
}
.serts__link:hover {
  text-decoration: underline;
  color: #fff;
}
.contacts__button {
  margin: 30px 0;
}
.input {
  border: none;
  border-bottom: 1px solid #DAD2E3;
  display: block;
  width: 100%;
  outline: 0;
  padding: 10px 2px 20px 0;
}
.input:focus {
  border-bottom: 1px solid #bb85ff;
}
.input::placeholder {
  color: transparent;
}
.row {
  position: relative;
  padding: 26px;
  margin-top: 30px;
}
.row--border {
  border-bottom: 1px solid #DAD2E3;
  padding-bottom: 10px;
}
.row__input {
  position: absolute;
  left: 0;
  top: 0;
}
.row__label {
  color: #A8A0B0;
  font-size: 20px;
  transition: all 0.1s ease-in-out;
  position: absolute;
  left: 0;
  top: 0;
  padding: 10px 2px 20px 0;
}
.row__input:not(:placeholder-shown) + label {
  font-size: 10px;
  margin: -8px 0 0 1px;
}
.row__select {
  margin: 20px 0 0;
  width: 160px;
}
.select {
  border: none;
  padding: 0;
}
.select:focus {
  outline: none;
}
.select__option {
  padding: 0;
}
