.nav {
    .list();
    display: flex;
    gap: 15px;

    @media @mobile {
        display: block;
    }

    &__item {
        @media @mobile {
            margin-bottom: 15px;
        }
    }

    &__link {
        font-size: 1.1rem;
        letter-spacing: 0.2px;
        font-weight: 500;
        color: @nav-color;
        text-decoration: none;
        &:hover {
            color: @color-text;
        }
        &:focus {
            color: @color-text;
        }
    }
}