.logo {
    position: relative;
    border-radius: 50% 0 50% 50%;
    background-color: @main-color;

    &__logo-text {
        position: relative;
        z-index: 100;
        padding-top: 5px;
        color: #fff;
        font-size: 26px;
        font-weight: bold;
        text-align: center;
    }

    &__inner-logo {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 50;
        width: 60%;
        height: 60%;
        border-radius: 50% 0 50% 0%;
        background-color: @accent-color;
    }


    &__link {
        display: inline-block;
        padding: 9px 49px;
        text-decoration: none;
        color: @accent-color;
        font-size: 1.3rem;

        &:hover {
            text-decoration: underline;
            color: @main-color;
        }
        &:focus {
            text-decoration: underline;
            color: @main-color;
        }
    }
}