@breakpoint-mobile: 601px;
@mobile: ~"(max-width: @{breakpoint-mobile})";

@main-color: #8643DC;
@accent-color: #FF59C1;
@gradient1: rgb(187, 133, 255);
@gradient2: rgb(255, 244, 251);
@color-title: #1B1C23;
@color-text: #47444E;
@form-border: #DAD2E3;
@nav-color: #7E7F86;

.list() {
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.margin-text(@val) {
    margin-top: 0;
    margin-bottom: @val;
}
