.about {
    .list();
    display: flex;
    gap: 10px;

    @media @mobile {
        flex-wrap: wrap;
    }

    &__card {
        flex-basis: 300px;
        flex-grow: 1;
        padding: 7px;
        background: linear-gradient(to bottom, @gradient2 0%, fade(@gradient2, 90) 38%, fade(@gradient1, 83) 100%);
    }

    &__title {
        .margin-text(20px);
        padding-top: 10px;
        font-weight: 500;
        color: @color-title;
        font-size: 1rem;
        letter-spacing: 0.4px;
    }

    &__details {
        line-height: 1.5;
        .margin-text(16px);
    }
}