.tech {
    
    &__column {}

    &__title {
        .margin-text(20px);
        padding-left: 60px;
        padding-top: 10px;
        padding-bottom: 10px;
        font-weight: 500;
        color: @color-title;
        font-size: 24px;
        letter-spacing: 0.4px;
        background-size: contain;
        background-repeat: no-repeat;

        &--html {
            background-image: url(../images/html.svg);
        }

        &--css {
            background-image: url(../images/css.svg);
        }

        &--js {
            background-image: url(../images/js.svg);
        }
    }

    &__description {
        .margin-text(0);
        font-size: 1rem;
        line-height: 200%;
        color: @color-text;
    }
}