.lessons {
    display: flex;
    justify-content: space-between;

    @media @mobile {
        display: block;
    }

    &__image-box {
        max-width: 50%;
        text-align: center;
        padding-top: 50px;

        @media @mobile {
            margin: 0 auto;
            padding-top: 20px;
        }
    }

    &__image {
        max-width: 100%;
    }

    &__schema {
        width: 50%;

        @media @mobile {
            width: 100%;
        }
    }

    &__mes-list {
        .list();
        margin: 40px 0 0 0;
        font-weight: 500;
        font-size: 20px;
        text-align: center;
    }

    &__mes-item {
        display: inline-block;

        &--es {
            width: 40%;
        }

        &--jus {
            width: 50%;
        }
    }

    &__order-list {
        .list();
        margin: 20px 0 40px;
        padding: 0 0 50px;
        background-image: url(../images/schema.png);
        background-image: url(../images/schema.webp);
        background-repeat: no-repeat;
        background-position: 45% 0px;
        background-size: contain;

        @media (min-resolution: 144dpi) {
            background-image: url(../images/schema@2x.png);
            background-image: url(../images/schema@2x.webp);
        }
    }

    &__order-item {

        &--left {
            padding-left: 10px;
            width: 40%;
        }

        &--right {
            width: 40%;
            margin-left: 55%;
        }

        &--m20 {
            margin-top: 20px;
        }

        &--m10 {
            margin-top: 10px;
        }
    }
}