.example {
    flex: 1 1 calc((100% - 20px) / 2);
    padding: 30px 45px 0;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    @media @mobile {
        border-radius: 0;
    }

    &--yellow {
        background: #fff2d9;
    }

    &--blue {
        background: #F3F1FA;
    }

    &--blue2 {
        background: rgba(51, 103, 153, .2);
    }

    &--cerin {
        background: fade(@gradient1, 20);
    }

    &--pink {
        background: @gradient2;
    }

    &--gaiszils {
        background: #d8ecff;
    }

    &--hidden {
        display: none;
    }

    &__title {
        padding-left: 0;
        padding-top: 0;
        padding-bottom: 5px;
        .margin-text(5px);
    }

    &__header {
        display: flex;
    }

    &__list {
        .list();
        .margin-text(20px);
        width: 60%;
        font-size: 1rem;
    }

    &__list-item {
        margin-bottom: 1px;

        &:first-child {
            color: @main-color;
            font-weight: 500;
            margin-bottom: 3px;
        }
    }

    &__logo {
        width: 18px;
        height: 18px;
        align-self: flex-end;
        margin-bottom: 15px;
        margin-left: 12%;
    }

    &__link {
        text-decoration: none;
        color: @accent-color;
        font-size: 16px;
        padding: 0 0 0 22px;

        &:hover {
            text-decoration: underline;
            color: @main-color;
        }
        &:focus {
            text-decoration: underline;
            color: @main-color;
        }
    }

    &__img {
        max-width: 100%;
    }
}