.content {

    &__title {
        font-weight: normal;
        font-size: 1rem;
        color: @main-color;
        .margin-text(40px);

        @media @mobile {
            text-align: center;
            border-bottom: 2px solid @main-color;
            padding-bottom: 2px;
        }

        &::before {
            content: "____";
            position: absolute;
            font-weight: normal;
            color: @main-color;
            font-size: 1.6rem;

            @media @mobile {
                content: '';
            }
        }
    }

    &__subtitle {
        margin-bottom: 0;
        color: @color-title;
        font-size: 2rem;
        letter-spacing: 1px;

        &--mb33 {
            .margin-text(33px);
        }

        &--mb23 {
            .margin-text(23px);
        }
    }

    &__grid {
        display: flex;
        gap: 30px;
        margin-bottom: 100px;

        @media @mobile {
            flex-wrap: wrap;
            margin-bottom: 50px;
        }
    }

    &__gallery {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        margin-bottom: 100px;

        @media @mobile {
            margin-bottom: 50px;
        }
    }

    &__technologies {
        margin-bottom: 100px;

        @media @mobile {
            margin-bottom: 50px;
        }
    }

    &__about {
        margin: 30px 0 100px;

        @media @mobile {
            margin-bottom: 50px;
        }
    }

    &__contacts {}
}