.intro {
    padding: 50px 0 0;
    display: flex;
    gap: 50px;

    @media @mobile {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 10px;
        display: block;
    }

    &__text-box {
        max-width: 40%;

        @media @mobile {
            max-width: 100%;
        }
    }

    &__title {
        .margin-text(33px);
        color: @color-title;
        font-size: 2.25rem;
        letter-spacing: 1px;
    }

    &__text {
        .margin-text(20px);
        font-size: 1.25rem;
        line-height: 170%;
    }

    &__image-box {
        max-width: 50%;
        text-align: center;

        @media @mobile {
            max-width: 100%;
            margin: 0 auto;
        }
    }

    &__image {
        max-width: 100%;
    }
}