.row {
    position: relative;
    padding: 26px;
    margin-top: 30px;


    &--border {
        border-bottom: 1px solid @form-border;
        padding-bottom: 10px;
    }

    &__input {
        position: absolute;
        left: 0;
        top: 0;
    }

    &__label {
        color: #A8A0B0;
        font-size: 20px;
        transition: all .1s ease-in-out;
        position: absolute;
        left: 0;
        top: 0;
        padding: 10px 2px 20px 0;
    }

    &__input:not(:placeholder-shown)+label {
        font-size: 10px;
        margin: -8px 0 0 1px;
    }

    &__select {        
        margin: 20px 0 0;        
        width: 160px;        
    }   

}